import { mapState } from 'vuex'
export default {
  name: 'articleFav',
  props: {
    article: {
      type: Object,
      required: true,
    },
  },
  computed: {
    ...mapState(['user']),
  },
  methods: {
    toEditor() {
      this.$router.push(`/editor/${this.article.slug}`)
    },
  },
}
