import {getComments,addComment} from '@/api/article'
export default {
  name:'comments',
  props:{
    article:{
      type:Object,
      required:true
    }
  },
  data(){
    return{
      comments:[],
      slug:this.article.slug,
      comment:{
        body:''
      }
    }
  },
  async mounted(){
    const {data} =  await getComments(this.slug)
    this.comments = data.comments
  },
  methods:{
    async myAddComment(){
      const res = await addComment({slug:this.slug,comment:this.comment})
      console.log(res);
    }
  }
}
